@import "../../../styles/variables";

.skills {
  min-height: 100vh;
  background-color: $back-dark;
  color: $text-dark;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  padding-top: 50px;
  padding-bottom: 100px;
  border-bottom: 50px dashed rgba($orange-color, 0.05);
  &.dark {
    background-color: $back-light;
    color: $text-light;
    border-bottom: 50px dashed rgba($orange-color, 0.6);
  }
}

.icons{
    padding: 10px;
    margin: 10px;
    border-radius: 1px solid $text-dark;
    font-size: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: 0s;
    cursor: pointer;
    &:hover{
        color: $orange-color;
        transform: translateY(-20px);
        transition: 0.15s;
    }
}

.tech{
    font-size: 15px;
    margin-top: 5px;
}

.skillsContainer{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.subtitle{
    margin-top: 40px;
    margin-bottom: 5px;
    color: $orange-color;
}
